<template>
    <div class="mall-order-detail">
      <div class="head">
        <div class="progress">
          <div class="pro-con">
            <div class="point" :class="status>=0?'active-point':''"></div>
            <div class="line"></div>
            <div class="point" :class="status>=1?'active-point':''"></div>
            <div class="line"></div>
            <div class="point" :class="status>=2?'active-point':''"></div>
          </div>
          <div class="pro-content">
            <div :class="status>=0 && status<=2?'active-content':''">兑换成功</div>
            <div :class="status>2 && status<4?'active-content':''">已发货</div>
            <div :class="status>=4?'active-content':''">已签收</div>
          </div>
        </div>
        <div v-if="status>=3" class="status" @click="toLogistics">
          <img src="@/assets/img/dingdanxiangqing--huoche.png" alt="">
          <div class="now-add-con">
            <p>{{orderWay}}</p>
            <p>{{orderTime}}</p>
          </div>
          <div class="iconfont icon-yousanjiao1"></div>
        </div>
        <div v-if="status<=2" class="status">
          <img src="@/assets/img/dingdanxiangqing--huoche.png" alt="">
          <div class="now-add-con">
           <p v-if="status==2">商家已接单</p>
            <p v-if="status==1">请尽快兑换</p>
          </div>
        </div>
      </div>
      <div class="order-detail">
         <div class="order-item">
          <div class="address">
            <span class="iconfont icon-dingwei"></span>
            <div>
              <p>{{order.receiver_address}}</p>
              <p>{{order.receiver_name}}    {{order.receiver_mobile}}</p>
            </div>
          </div>
          <div class="order-info">
            <img :src="orderImg" alt="">
            <div class="order-detail">
              <p class="order-title">{{order.goods_name}}</p>
              <div>积分*{{order.goods_fee }}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="memo" v-if="len>0">
        {{order.buyer_message}}
      </div>
      <div class="memo" v-if="len==0">
        备注
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script type="text/ecmascript-6">
import { apiPost, apiGet } from "../../common/js/api.js"
export default {
    name: "mall_order_detail",
    
    data(){
      const query = this.$route.query;
      return{
        orderId:query.orderId||query.order_id,
        status:0,
        orderWay:'',
        orderTime:'',
        len:0,
        order:{
          img:"https://ntr-test.oss-cn-beijing.aliyuncs.com/image/2b97d627b671497f92e8b0044d66c187.jpg",
          status:"已完成",
          color:"红色",
          size:"S",
          title:"我是商品的标题我是商品的标题我是商品的标题",
          bonusPoint:20,
          addressName:"天府三街新希望",
          mobile:185425671,
          userName:"王大锤",
          memo:"备注信息",

        },
        orderImg:''
      }
    },
    components: { },
    computed: {},
    watch:{},
    beforeCreate(){

    },
    created(){
      console.log(this.$route.query)
      this.accessToken=this.$cookie.get('accessToken')
    /*  this.accessToken='2eHG5laajTo6Z1kPthFiJFc4D_hHzj0R';*/
      this.getMallOrderDetail();
    },
    beforeMount(){

    },
    mounted(){

    },
    methods:{
      //获取订单详情
      getMallOrderDetail(){
        let formdata = new FormData();
        formdata.append('accessToken',this.accessToken);
        formdata.append('order_id',this.orderId)
        apiPost("/order/order-details",formdata).then(res=>{
          console.log(res)
          this.order= res.data.data.data.order;
          this.len=this.order.buyer_message.length,
          this.status=this.order.status
          this.orderWay=res.data.data.data.data.context;
          this.orderTime=res.data.data.data.data.time;
          console.log(this.order.goods_img[0],this.status )
            if(this.order.goods_img){
              this.orderImg = this.order.goods_img[0] || '';
            }

        })
      },
      //前往物流信息
      toLogistics(){
        this.$router.push({
          path:"logisticsInfo",
          query:{
            order_id:this.orderId
          }
        })
      }
    }
}
</script>

<style scoped lang="scss">
  .mall-order-detail{
    background: #E6E9ED;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .head{
    height:1.58rem;
    width: 100%;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(127,173,249,1));
    margin-bottom: .6rem;
    .progress{
      padding: .48rem .34rem 0 .34rem;
      &>div{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
      }
      .pro-con{
        padding:0 .28rem;
        align-items: center;
        height: .08rem;
        width: 100%;
        .point{
          width: .04rem;
          height: .04rem;
          border-radius: 50%;
          background: #fff;
        }
        .active-point{
          width: .08rem;
          height:.08rem;
        }
        .line{
          flex:1;
          height: .01rem;
          border:0;
          border-bottom: .01rem dotted #fff;
        }
      }
      .pro-content{
        height: .33rem;
        width: 100%;
        color: #fff;
        font-size: .15rem;
        &>div{
          flex:1;
          &:nth-child(2){
            text-align: center
          }
          &:nth-child(3){
            text-align: right
          }
        }
        .active-content{
          font-size: .17rem
        }
      }
    } 
    .status{
      width: 3.5rem;
      height: .81rem;
      background: #fff;
      border-radius: .04rem;
      padding:0 .13rem;
      margin: 0 auto;
      display: flex;
      margin-top: .31rem;
      align-items: center;
      justify-content: space-between;
      img{
        width:.5rem;
        height: .3rem;
        margin-right: .2rem;
      }
      .now-add-con{
        flex: 1;
        p:nth-of-type(1){
          font-size: .16rem;
          color: #333;
          font-family:'SourceHanSansCN-Regular';
          margin-bottom: .05rem;
        }
        p:nth-of-type(2){
          font-size: .15rem;
          color: #9A9A9A;
        }
      }
      .icon-yousanjiao1{
        color: #D7D7D7
      }
    }
  }
  .memo{
    width:3.5rem;
    height: .81rem;
    border-radius: .04rem;
    background: #fff;
    margin:0 auto;
    position: relative;
    padding:.07rem .13rem;
    font-size: .15rem;
    color:#999999;
  }
  .order-detail{
    padding: 0 .13rem;
    height: 1.96rem;
    margin-bottom: .13rem;
    .address{
      display: flex;
      overflow: hidden;
      justify-content: space-between;
      align-items: center;
      height: .8rem;
      padding: 0 .09rem;
      border-bottom: .01rem solid #F5F5F5;
      .icon-dingwei{
        font-size: .17rem;
        color:#BFC0C0;
        margin-right: .13rem;
      };
      &>div{
        flex:1;
        overflow: hidden;
        p:nth-of-type(1){
          color: #333;
          font-size: .17rem;
          margin-bottom: .05rem;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          width:100%;
        }
        p:nth-of-type(2){
          color: #9A9A9A;
          font-size: .15rem;
        }
      }
    }
  }
  .order-item{
      width:100%;
      height: 1.96rem;
      background: #fff;
      margin-bottom: .17rem;
      padding: 0 .04rem 0 .04rem;
      border-radius: .04rem;
      display: flex;
      flex-direction: column;
      position: relative;
      .order-info{
        flex:1;
        flex-direction: row;
        display: flex;
        padding: .21rem .09rem 0;
        position: relative;
        &>img{
          width:.73rem;
          height: .73rem;
          margin-right: .13rem;
        }
        .order-detail{
          flex:1;
          overflow: hidden;
          height: 100%;
          padding: 0;
          .order-title{
            text-overflow: ellipsis;
            white-space: nowrap; 
            overflow: hidden;
            width: 100%;
          }
          .order-title{
            color:#333333;
            font-size: .17rem;
          }
          &>div{
            color:#9A9A9A;
            font-size: .13rem;
            margin: .04rem 0;
          }
        }
      }
      .order-option{
        height: .5rem;
        border-top: .01rem solid #F5F5F5;
        align-items: center;
        justify-content: flex-end;
        display: flex;
        padding-right: .09rem;
      }
    }
</style>



